<template>
	<view class="page bg-white">
		<cu-custom bgColor="bg-white" :isBack="false"></cu-custom>
		<view class="text-center margin-xl">
			<view class="cu-avatar round xxl" :style="'border-radius: 20upx;background-image: url('+ysz_sign_logo+');'"></view>
			<view class="text-black text-bold text-xxl margin-top-sm">云书展</view>
		</view>
		<form @submit="formSubmit" report-submit>
			<view class="cu-form-group margin-top">
				<text class='cuIcon-sign-accn margin-right-xs'></text>
				<input placeholder="请输入手机号" placeholder-style="color:#B2B2B2" v-model='account' name="account" type="number"></input>
				<view class="cu-form-x"></view>
			</view>
			<view class="cu-form-group">
				<text class='cuIcon-sign-code margin-right-xs'></text>
				<input placeholder="请输入手机验证码" placeholder-style="color:#B2B2B2" type="text" name="smsCode" v-model='smsCode'></input>
				<view v-if="yzm && !yzm_disabled" class="title text-yellow2" @click="getYzm">获取验证码</view>
				<view v-if="yzm && yzm_disabled" class="title hui">获取验证码</view>
				<view v-if="!yzm" class="title hui">{{djs}}s 后重新发送</view>
				<view class="cu-form-x"></view>
			</view>
			<view class="cu-form-group">
				<text class='cuIcon-sign-conf margin-right-xs'></text>
				<input placeholder="请输入密码" placeholder-style="color:#B2B2B2" type="password" v-model='password' name="password"></input>
				<view class="cu-form-x"></view>
			</view>
			<view class="cu-form-group">
				<text class='cuIcon-sign-pasw margin-right-xs'></text>
				<input placeholder="请输入确认密码" placeholder-style="color:#B2B2B2" type="password" v-model='resPassword' name="resPassword"></input>
				<view class="cu-form-x"></view>
			</view>
			 <view style="">
			 	<view class="padding flex flex-direction" style="">
			 		<button class="cu-btn bg-gradual-green88 lg round" :disabled="submit_disabled" style="height: 100upx;width:100%;color:#fff" formType="submit">注册</button>
			 	</view>
				<view>
					<view @tap="tologin()" class="text-gray2 text-left margin-right" style="float: right;">立即登录</view>
				</view>
			 </view>
		</form>
		
		<!-- <view class="padding-lr">
			
			<text :style="[{color:agreeColor}]" class='cuIcon-roundcheckfill margin-right-sm' style="float: left;font-size: 40upx;"></text>
			<text class="text-gray2">阅读并同意</text>
			<text class="text-yellow2">《注册协议条款》</text>
		</view> -->
	</view>
</template>

<script>
	var ysz_sign_logo='data:image/png;base64,';
	
	export default {
		data() {
			return {
				agreeColor:'#DCDCDC' ,//#F97F15   #DCDCDC
				account: "", //手机号
				smsCode: "", //验证码
				password: "", //密码
				resPassword: "", //确认密码
				yzm: true, //验证码
				djs: 60, //倒计时
				yaoQm: "", //邀请码
				isCheck: true ,//是否勾选协议
				msg:"",
				submit_disabled:false,
				yzm_disabled:false,
				ysz_sign_logo:ysz_sign_logo
			};
		},
		onShow() {
			console.log("success")
		},
		methods:{
			//注册
			tologin(){
				uni.navigateTo({
					url:"./login"
				})
			},
			//手机验证码
			getYzm() {
				 
				let that = this;
				if(that.account.length != 11){
					uni.showToast({
						title: "请输入正确的手机号",
						icon: "none"
					});
					return;
				}
				that.yzm_disabled=true;
				
				this.GetData('/check_phone',{"phone":that.account},function(res){
					if(res.code==200)
					{
						that.send_sms();
					}else{
						uni.showToast({
							title: res.msg,
							icon: "none"
						});
						that.yzm_disabled=false;
					}
				},'GET',function(){
					uni.stopPullDownRefresh();
				})
				return ;	
			},
			send_sms(){
				let that=this;
				this.GetData('/user/send_sms',{"phone":that.account,"is_type":2},function(res){
					uni.showToast({
						title: res.msg,
						icon: "none"
					});
					that.yzm_disabled=false;
					
					if(res.code==200){ 
						that.yzm = !that.yzm;
						that.timer = setInterval(() => {
							if (that.djs == 0) {
								that.yzm = !that.yzm;
								that.djs = 60;
								clearInterval(that.timer);
							}
							that.djs = that.djs - 1;
						}, 1000);
					}
				},'post',function(){
					uni.stopPullDownRefresh();
				})
			},
			//确认注册
			formSubmit(e) {
				let that = this;
				
				//进行表单检查
				var formData = e.detail.value;
				
				if(this.isEmpty(formData.account)){
					uni.showToast({
						title: '请输入手机号!',
						icon: "none"
					});
					return;
				}
				if(formData.account.length != 11){
					uni.showToast({
						title: "请输入正确的手机号",
						icon: "none"
					});
					return;
				}
				if(this.isEmpty(formData.smsCode)){
					uni.showToast({
						title: '请输入验证码!',
						icon: "none"
					});
					return;
				}
				if(this.isEmpty(formData.password)){
					uni.showToast({
						title: '请输入密码!',
						icon: "none"
					});
					return;
				}
				if(this.isEmpty(formData.resPassword)){
					uni.showToast({
						title: '请输入确认密码!',
						icon: "none"
					});
					return;
				}
				
				if(formData.password!=formData.resPassword){
					uni.showToast({
						title: '两次密码不一致!',
						icon: "none"
					});
					return;
				}
				that.zuce(formData);
				 
			},
			zuce(formData){
				let that = this;
				let data={
						phone: formData.account,
						password: formData.password,
						inviter_id: 0,
						password_comp: formData.password,
						code: formData.smsCode
					};
				that.submit_disabled=true;
				this.GetData('/user/register',data,function(res){
					that.submit_disabled=false;
					
					if(res.code==200){
						uni.showToast({
							title: '注册成功~!',
							icon: "none"
						});
						setTimeout(()=>{
							uni.reLaunch({
								url:'/pages/my/login'
							})
						},1000)
					}else{
						uni.showToast({
							title: res.msg,
							icon: "none"
						});
					}
					
				},'post',function(){
					uni.stopPullDownRefresh();
				})
			}
		}
	}
</script>

<style>
	.page {
		height: 100vh;
		width:100vw;
	}
	.cu-form-group{
		position: relative;
		border-top:0px solid #eee;
	}
	.cu-form-x{
		width:calc(100% - 120upx);
		height: 0.5upx;
		background-color: #EEEEEE;
		position: absolute;
		bottom:0.5upx;
		margin-left:50upx;
	}
	.hui {
		color: #E1E1E1;
	}
	.cu-form-group+.cu-form-group {
		border-top:0px solid #eee;
	}
</style>
